import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { AuthService } from '@core/auth/auth.service';
import { environment } from 'environments/environment';
import { Observable, Subject } from 'rxjs';
import { IEntregaQR } from '../models/entrega-qr';

@Injectable()
export class EntregasQRService {
    constructor(
        private http: HttpClient, 
        private _authService: AuthService
    ) { }

    /**
     * Funcion en el Servicio 
     * @param entregas 
     * @returns Crea un item nuevo
     */
    saveEntregaQR(entregas: IEntregaQR): Observable<IEntregaQR> {
        return this.http.post<IEntregaQR>(`${environment.stock.entregas}`, entregas);
    }
    
    /**
     * 
     * @param codigo 
     * @returns Verifica si el QR escaneado de la Credencial del Usuario es valido.
     */
    getUsuarioQR(codigo: number): Observable<any> {
        return this.http.get<any>(`${environment.login.view_usuarios}?filter[codigo]=`+ codigo );
    }

    /**
     * 
     * @param idventagenerica 
     * @returns Verifica si el QR escaneado de la Orden de Servicio es valido.
     */
    getOrdenServicio(idventagenerica: number): Observable<any> {
        return this.http.get<any>(`${environment.ventas.view_venta_genericas}?filter[idventagenerica]=`+ idventagenerica)
    }

    /**
     * 
     * @param idarticulo 
     * @returns Verifica si el QR escaneado de las Etiquetas de Articulos es valido.
     */
    getArticuloQR(idarticulo: number): Observable<any> {
        return this.http.get<any>(`${environment.stock.view_articulos}?filter[idarticulo]=`+ idarticulo);
    }

    /**
     * 
     * @param option 
     * @returns Trae los Depositos de dichas empresa
     */
    getDepositos(option?) {
        let f = '?';
        f += !option ? 'filter[activo]=1' : '';
        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.stock.depositos}${f}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token;
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable()
    }

    /**
     * 
     * @param deposito 
     * @returns Trae los Articulos que se tienen en dichos Depositos.
     */
    getArticulosDepositos(deposito: number) {
        let f = `?filter[deposito]=${deposito}`
        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.stock.view_stock_articulo_depositos}${f}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token;
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable()
    }

}