import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { Observable, Subject, firstValueFrom, map, startWith, takeUntil } from 'rxjs';
import { MatTableDataSource } from '@angular/material/table'
import { FuseConfirmationService } from '@fuse/services/confirmation';
import { AppAlertService } from '@shared/service/app/alert.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { TablaDinamicaComponent } from '@shared/components/tabla-dinamica/tabla-dinamica.component';
import { MatDialog } from '@angular/material/dialog';
import { SearchService } from 'app/layout/common/search/search.service';
import { JedsMenuService } from '@shared/service/app/jeds-menu.service';
import { HeaderTextService } from '@shared/components/header-text/header-text.service';
import { Router } from '@angular/router';
import { EmpresaService } from '@shared/service/app/empresa.service';
import { PwaService } from '@shared/service/app/pwa.service';
import { LectorQrComponent } from '@shared/components/lector-qr/lector-qr.component';
import { EntregasQRService } from '../service/entregas-qr.service';
import { IEntregaQR } from '../models/entrega-qr';
import { IDeposito } from '../../depositos/models/deposito';
import { SaveComponent } from './save/save.component';
import { IUsuario } from '@core/user/user.types';
import { DatePipe } from '@angular/common';

@Component({
    selector: 'app-entregas-qr',
    templateUrl: './entregas-qr.component.html',
    styleUrls: ['./entregas-qr.component.scss']
})
export class EntregasQRComponent implements OnInit, OnDestroy {
    form: FormGroup;
    columns = ['articulo', 'unidadmedida', 'cantidad'];
    public dataSource: MatTableDataSource<any> = new MatTableDataSource();
    user: any;
    entrega: IEntregaQR;
    depositos: IDeposito[] = [];
    mov_art_list: any[] = [];
    usuarios: IUsuario[] = [];
    filteredDepositos: Observable<IDeposito[]>;
    color_primario: string;
    color_secundario: string;
    default_color: string = 'bg-primary';
    private _unsubscribeAll: Subject<any> = new Subject<any>();
    idUsuario: number;
    nombre: string = '';
    apellido: string = '';
    orden_servicio: number;
    mostrarTextoCredencial: boolean = true;
    mostrarTextoOrdenServicio: boolean = true;
    buttonStyleCredencial: string = 'default';
    buttonStyleOrdenServicio: string = 'default';
    credencialScaneada = false;
    ordenServicioScaneada = false;

    constructor(
        private _entregasQRService: EntregasQRService,
        private _empresaService: EmpresaService,
        private confirm: FuseConfirmationService,
        private alert: AppAlertService,
        private dialogo: MatDialog,
        private _searchService: SearchService,
        private _menuService: JedsMenuService,
        private router: Router,
        public _headerTextService: HeaderTextService,
        private pwaService: PwaService,
        private datePipe: DatePipe

    ) {
        this._headerTextService.setTitulo(this.router);
        this.user = JSON.parse(localStorage.getItem('user'))
    }

    @ViewChild(TablaDinamicaComponent) tabla: TablaDinamicaComponent;
    ngOnInit(): void {
        // Subscribe to empresa data
        this._empresaService.empresa$
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe(empresa => {
                this.color_primario = empresa.color_primario;
                this.color_secundario = empresa.color_secundario;
            });

        // Load empresa data
        this._empresaService.getEmpresa();
        this.newForm();
        this.getDepositos();
    }


    newForm() {
        this.form = new FormGroup({
            deposito_entrega: new FormControl((this.entrega ? this.entrega.descripcion_deposito : ''), Validators.required),
        });
    }

    /**
     * Se escanea el QR de la Credencial de Usuario y se toma el idusuario.
     */
    credencialUsuario() {
        this.dialogo.open(LectorQrComponent, {
            panelClass: 'ayuda-dialog'
        }).componentInstance.sendObject.subscribe((code) => {
            if (!code || code.length === 0) {
                this.alert.error('Código QR vacío o inválido.');
                return;
            }
            const idusuario = parseInt(code, 10);
            this.idUsuario = idusuario;
            this._entregasQRService.getUsuarioQR(idusuario).subscribe(response => {
                if (response?.result?.length > 0) {
                    const usuario = response.result[0];
                    this.nombre = usuario.nombre;
                    this.apellido = usuario.apellido;
                    this.mostrarTextoCredencial = false;
                    this.buttonStyleCredencial = 'scanned';
                    this.credencialScaneada = true;
                } else {
                    this.alert.error('Usuario no encontrado.');
                }
            }, error => {
                console.error('Error al obtener usuario:', error);
            });
        });
    }

    /**
     * Se escanea el QR de Orden de Servicio y se toma la idventagenerica.
     */
    ordenServicio() {
        this.dialogo.open(LectorQrComponent, {
            panelClass: 'ayuda-dialog'
        }).componentInstance.sendObject.subscribe((code) => {
            if (!code || code.length === 0) {
                this.alert.error('Código QR vacío o inválido.');
                return;
            }
            const idventagenerica = parseInt(code, 10);
            this._entregasQRService.getOrdenServicio(idventagenerica).subscribe(response => {
                if (response?.result?.length > 0) {
                    const venta = response.result[0];
                    this.orden_servicio = venta.idventagenerica;
                    this.mostrarTextoOrdenServicio = false;
                    this.buttonStyleOrdenServicio = 'scanned';
                    this.ordenServicioScaneada = true;
                } else {
                    this.alert.error('Orden de Servicio no encontrada.');
                }
            }, error => {
                console.error('Error al obtener venta:', error);
            });
        });
    }

    /**
     * 
     * @param option 
     * Se va a traer los Depositos para listarlos en un Select
     */
    async getDepositos(option?) {
        const Depositos = await firstValueFrom(this._entregasQRService.getDepositos(option));
        new Promise(async (resolve) => {
            this.depositos = Depositos.list;
            this.filteredDepositos = this.form.get('deposito_entrega').valueChanges.pipe(
                startWith(''),
                map((value) => this._filterDepositos(value)),
            );

            resolve(true);
        })
    };

    /**
     * 
     * @param deposito 
     * @returns Filtrado de Depositos para el Autocomplete
     */
    private _filterDepositos(deposito: IDeposito): IDeposito[] {
        const filterValue = deposito.toString().toLowerCase();
        return this.depositos.filter(deposito => deposito.descripcion.toLowerCase().includes(filterValue));
    }

    getTextDeposito(option) {
        if (option.descripcion) {
            return option.descripcion;
        }
        if (option.deposito) {
            return option.deposito;
        }
        return option;
    }

    /**
     * Esta funcion guarda un item
     */
    agregarArticulo() {
        if (this.form.get('deposito_entrega').value != '') {
            let deposito_entrega_value = this.form.get('deposito_entrega').value.iddeposito;
            this.dialogo
                .open(SaveComponent, {
                    panelClass: 'ayuda-dialog',
                    data: {
                        deposito_entrega: deposito_entrega_value,
                        movimientos: this.mov_art_list,
                    }
                })
                .afterClosed()
                .subscribe({
                    next: (res) => {
                        if (res) {
                            let movimiento_articulo = {
                                idarticulo: res.articulo.idarticulo,
                                cantidad: res.cantidad,
                                idunidadmedida: res.unidad_medida,
                                descripcion_articulo: res.articulo.descripcion,
                                descripcion_um: res.unidadmedida_descripcion,
                                codigo_interno: res.articulo.codigo_interno,
                                stock: res.stock,    //Stock restante de ese articulo con esa um...
                            }
                            this.mov_art_list = [...this.mov_art_list, movimiento_articulo];
                            this.dataSource = new MatTableDataSource(this.mov_art_list); //Actualizo la tabla

                            // Deshabilito el control Deposito
                            if (this.mov_art_list.length != 0) {
                                this.form.get('deposito_entrega').disable();
                            }

                            this.alert.success("Artículo añadido con éxito")
                        }
                    },
                });
        } else {
            this.alert.warning("Seleccione un Depósito de origen")
        }
    }

    /**
    * Funcion para limpiar campos
    */
    limpiarCasillero(input) {
        this.form.get(input).setValue('');
    }

    /**
     * Resetea todo el Formulario de Entrega con QR 
     */
    resetForm() {
        this.form.get('deposito_entrega').setValue('');
        this.form.get('deposito_entrega').enable();
        this.form.get('deposito_entrega').setErrors(null);
        this.mov_art_list = [];
        this.dataSource = new MatTableDataSource(this.mov_art_list);
        this.mostrarTextoCredencial = true;
        this.mostrarTextoOrdenServicio = true;
        this.buttonStyleCredencial = 'default';
        this.buttonStyleOrdenServicio = 'default';
        this.credencialScaneada = false;
        this.ordenServicioScaneada = false;
        this.idUsuario = null;
        this.nombre = '';
        this.apellido = '';
        this.orden_servicio = null;
    }

    onSubmit() {
        // if (!this.credencialScaneada || !this.ordenServicioScaneada) {
        //     this.alert.error('Escanea ambos códigos QR');
        //     return;
        // }

        if (!this.ordenServicioScaneada) {
            this.alert.error('Debe Escanear La Orden De Servicio');
            return;
        }

        if (!this.form.get('deposito_entrega').value || !this.form.get('deposito_entrega').value.iddeposito) {
            this.alert.error('Revise Los Datos Ingresados');
            return;
        } else {
            let movimientos = this.mov_art_list.map(objeto => ({   //Encapsulo solamente los datos que utilizaré de los articulos...
                idarticulo: objeto.idarticulo,
                cantidad: objeto.cantidad,
                idunidadmedida: objeto.idunidadmedida,
                idventa: this.orden_servicio
            }))

            if (movimientos.length === 0) {
                return this.alert.warning('Debe ingresar un articulo a la orden de entrega.')
            }

            const fecha = new Date().toISOString().split('T')[0];
            const hora = new Date().toLocaleTimeString();

            // Se formatea la fecha nuevamente al formato deseado (YYYY-MM-DD)
            const fecha_formateada = this.datePipe.transform(fecha, 'yyyy-MM-dd');

            // Se concatena a ambas fecha y hora
            const fecha_hora = `${fecha_formateada} ${hora}`;

            const payload = {
                entrega: {
                    idusuario: this.user.id,
                    fecha_hora: fecha_hora,
                    iddeposito: this.form.get('deposito_entrega').value.iddeposito,
                    usuario_entrega: this.idUsuario ? this.idUsuario : null,
                    observaciones: `Entrega QR - Órden N°${this.orden_servicio}`,
                },
                movimientos: movimientos
            }

            this._entregasQRService.saveEntregaQR(payload)
                .subscribe({
                    next: (data) => {
                        if (data) {
                            this.alert.success("Entrega creada con éxito.");
                            setTimeout(() => {
                                this.resetForm();
                            }, 1500);
                        }
                    }
                });
        }
    }

    ngOnDestroy(): void {
        this._searchService.unsubscribe();
        this.dialogo.closeAll();
    }
}